'use client';

import React, { useRef, useEffect, useCallback, useState } from 'react';
import * as THREE from 'three';
import { Button } from '@/components/ui/button';
import { createRubiksCube, scrambleCube, resetCube, rotateCube, rotateFace, isSolved } from '@/lib/rubikCubeUtils';

const RubikCubeGame = () => {
  // State to track WebGL support and cube state
  const [webGLError, setWebGLError] = useState<string | undefined>(undefined);
  const [isCubeSolved, setIsCubeSolved] = useState(true);
  const containerRef = useRef<HTMLDivElement>(null);
  const rendererRef = useRef<THREE.WebGLRenderer | null>(null);
  const sceneRef = useRef<THREE.Scene | null>(null);
  const cameraRef = useRef<THREE.PerspectiveCamera | null>(null);
  const cubeRef = useRef<THREE.Group | null>(null);
  const animationFrameRef = useRef<number | null>(null);
  const [isMouseDown, setIsMouseDown] = useState(false);
  const [isTouchDown, setIsTouchDown] = useState(false);
  const [lastMousePosition, setLastMousePosition] = useState({ x: 0, y: 0 });

  // Initialize Three.js scene
  const initScene = useCallback(() => {
    if (!containerRef.current) return;

    try {
      // Initialize scene
      const scene = new THREE.Scene();
      scene.background = new THREE.Color(0x121212);
      sceneRef.current = scene;

      // Initialize camera with better position
      const camera = new THREE.PerspectiveCamera(
        60,
        containerRef.current.clientWidth / containerRef.current.clientHeight,
        0.1,
        1000
      );
      camera.position.set(6, 4, 8);
      camera.lookAt(0, 0, 0);
      cameraRef.current = camera;

      // Initialize renderer
      const renderer = new THREE.WebGLRenderer({
        antialias: true,
        alpha: true,
        powerPreference: 'default' as WebGLPowerPreference
      });

      if (!renderer.getContext()) {
        throw new Error('Failed to initialize WebGL context');
      }
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.setSize(containerRef.current.clientWidth, containerRef.current.clientHeight);
      containerRef.current.appendChild(renderer.domElement);
      rendererRef.current = renderer;

      // Add lights
      const ambientLight = new THREE.AmbientLight(0xffffff, 0.6);
      scene.add(ambientLight);

      // Add directional lights from multiple angles
      const createDirectionalLight = (x: number, y: number, z: number, intensity: number = 0.8) => {
        const light = new THREE.DirectionalLight(0xffffff, intensity);
        light.position.set(x, y, z);
        return light;
      };

      scene.add(createDirectionalLight(10, 10, 10));   // Top-right-front
      scene.add(createDirectionalLight(-10, 10, 10));  // Top-left-front
      scene.add(createDirectionalLight(10, -10, 10));  // Bottom-right-front
      scene.add(createDirectionalLight(0, 10, -10));   // Top-back

      // Create Rubik's cube
      const cube = createRubiksCube();
      scene.add(cube);
      cubeRef.current = cube;

      // Animation loop with smooth auto-rotation
      const animate = () => {
        animationFrameRef.current = requestAnimationFrame(animate);
        if (rendererRef.current && sceneRef.current && cameraRef.current && cubeRef.current) {
          if (!isMouseDown && !isTouchDown) {
            // Add subtle auto-rotation when not being dragged or touched
            cubeRef.current.rotation.y += 0.005;
          }
          rendererRef.current.render(sceneRef.current, cameraRef.current);
        }
      };
      animate();

    } catch (error) {
      setWebGLError(error instanceof Error ? error.message : 'Failed to initialize WebGL');
      return;
    }

    // Handle window resize
    const handleResize = () => {
      if (!containerRef.current || !cameraRef.current || !rendererRef.current) return;

      const width = containerRef.current.clientWidth;
      const height = containerRef.current.clientHeight;

      cameraRef.current.aspect = width / height;
      cameraRef.current.updateProjectionMatrix();
      rendererRef.current.setSize(width, height);
      rendererRef.current.setPixelRatio(window.devicePixelRatio);
    };

    window.addEventListener('resize', handleResize);

    // Handle keyboard controls
    const handleKeyDown = (event: KeyboardEvent) => {
      if (!cubeRef.current) return;

      const angle = event.shiftKey ? -Math.PI / 2 : Math.PI / 2;
      
      switch (event.key.toLowerCase()) {
        case 'f': // Front face
          rotateFace(cubeRef.current!, 'z', 1, angle);
          break;
        case 'b': // Back face
          rotateFace(cubeRef.current!, 'z', -1, angle);
          break;
        case 'r': // Right face
          rotateFace(cubeRef.current!, 'x', 1, angle);
          break;
        case 'l': // Left face
          rotateFace(cubeRef.current!, 'x', -1, angle);
          break;
        case 'u': // Up face
          rotateFace(cubeRef.current!, 'y', 1, angle);
          break;
        case 'd': // Down face
          rotateFace(cubeRef.current!, 'y', -1, angle);
          break;
      }

      // Check if cube is solved after each move
      if (cubeRef.current) {
        setIsCubeSolved(isSolved(cubeRef.current));
      }
    };

    window.addEventListener('keydown', handleKeyDown);

    // Cleanup
    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('keydown', handleKeyDown);
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
      if (containerRef.current && rendererRef.current) {
        containerRef.current.removeChild(rendererRef.current.domElement);
      }
      rendererRef.current?.dispose();
    };
  }, []);

  // Initialize scene on component mount
  useEffect(() => {
    initScene();
  }, [initScene]);

  // Event handlers
  const handleMouseDown = useCallback((e: React.MouseEvent) => {
    setIsMouseDown(true);
    setLastMousePosition({
      x: e.clientX,
      y: e.clientY,
    });
  }, []);

  const handleMouseMove = useCallback((e: React.MouseEvent) => {
    if (!isMouseDown || !cubeRef.current) return;

    const deltaX = e.clientX - lastMousePosition.x;
    const deltaY = e.clientY - lastMousePosition.y;

    rotateCube(cubeRef.current, deltaX, deltaY);

    setLastMousePosition({
      x: e.clientX,
      y: e.clientY,
    });
  }, [isMouseDown, lastMousePosition]);

  const handleMouseUp = useCallback(() => {
    setIsMouseDown(false);
  }, []);

  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    const touch = e.touches[0];
    setIsTouchDown(true);
    setLastMousePosition({
      x: touch.clientX,
      y: touch.clientY,
    });
  }, []);

  const handleTouchMove = useCallback((e: React.TouchEvent) => {
    if (!isTouchDown || !cubeRef.current) return;

    const touch = e.touches[0];
    const deltaX = touch.clientX - lastMousePosition.x;
    const deltaY = touch.clientY - lastMousePosition.y;

    rotateCube(cubeRef.current, deltaX, deltaY);

    setLastMousePosition({
      x: touch.clientX,
      y: touch.clientY,
    });
  }, [isTouchDown, lastMousePosition]);

  const handleTouchEnd = useCallback(() => {
    setIsTouchDown(false);
  }, []);

  // Game control handlers
  const [selectedLayer, setSelectedLayer] = useState<{ axis: 'x' | 'y' | 'z'; layer: number } | null>(null);
  const [isRotating, setIsRotating] = useState(false);

  const handleScramble = useCallback(() => {
    if (cubeRef.current && !isRotating) {
      scrambleCube(cubeRef.current);
      setIsCubeSolved(false);
    }
  }, [isRotating]);

  const handleReset = useCallback(() => {
    if (cubeRef.current && !isRotating) {
      resetCube(cubeRef.current);
      setIsCubeSolved(true);
    }
  }, [isRotating]);

  // Rotate selected layer clockwise or counterclockwise
  const rotateSelectedLayer = useCallback((clockwise: boolean) => {
    if (!cubeRef.current || !selectedLayer || isRotating) return;

    setIsRotating(true);
    const angle = clockwise ? Math.PI / 2 : -Math.PI / 2;

    // Animate rotation over 300ms
    const duration = 300;
    const start = performance.now();

    const animateRotation = (time: number) => {
      const elapsed = time - start;
      const progress = Math.min(elapsed / duration, 1);
      const currentAngle = angle * progress;

      // Reset rotation before applying incremental rotation
      resetCube(cubeRef.current!);
      rotateFace(cubeRef.current!, selectedLayer.axis, selectedLayer.layer, currentAngle);

      if (progress < 1) {
        requestAnimationFrame(animateRotation);
      } else {
        setIsRotating(false);
        setIsCubeSolved(isSolved(cubeRef.current!));
      }
    };

    requestAnimationFrame(animateRotation);
  }, [selectedLayer, isRotating]);

  // UI for selecting layer and rotating
  const LayerSelector = () => {
    const layers = [-1, 0, 1];
    const axes: Array<'x' | 'y' | 'z'> = ['x', 'y', 'z'];

    return (
      <div className="mt-6 flex flex-col gap-4">
        {axes.map((axis) => (
          <div key={axis} className="flex items-center gap-2">
            <span className="text-white font-semibold capitalize">{axis.toUpperCase()} axis:</span>
            {layers.map((layer) => (
              <button
                key={layer}
                className={`px-3 py-1 rounded ${
                  selectedLayer?.axis === axis && selectedLayer.layer === layer
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-700 text-gray-300'
                }`}
                onClick={() => setSelectedLayer({ axis, layer })}
                disabled={isRotating}
              >
                {layer === -1 ? '1' : layer === 0 ? '2' : '3'}
              </button>
            ))}
          </div>
        ))}
        <div className="flex gap-4 mt-2">
          <button
            className="bg-green-600 text-white px-4 py-2 rounded disabled:opacity-50"
            onClick={() => rotateSelectedLayer(true)}
            disabled={!selectedLayer || isRotating}
          >
            Girar ‚Üª
          </button>
          <button
            className="bg-red-600 text-white px-4 py-2 rounded disabled:opacity-50"
            onClick={() => rotateSelectedLayer(false)}
            disabled={!selectedLayer || isRotating}
          >
            Girar ‚Ü∫
          </button>
        </div>
      </div>
    );
  };


  if (webGLError) {
    return (
      <div className="w-full h-screen flex flex-col items-center justify-center bg-black text-white p-4">
        <h1 className="text-2xl font-bold mb-4">Error</h1>
        <p className="text-red-500">{webGLError}</p>
        <p className="mt-2">Por favor, intenta usar un navegador m√°s moderno o habilita el soporte de WebGL.</p>
      </div>
    );
  }

  return (
    <div className="w-full h-screen flex flex-col items-center justify-center bg-gradient-to-b from-gray-900 to-black p-4">
      <div className="text-center mb-6">
        <h1 className="text-5xl font-bold text-white mb-4 tracking-tight">Cubo Rubik</h1>
        <div className="space-y-2 text-gray-300">
          <p className="text-lg">üñ±Ô∏è Arrastra para rotar el cubo</p>
          <p className="text-sm">El cubo gira autom√°ticamente cuando no lo est√°s moviendo</p>
        </div>
      </div>
      
      <div
        ref={containerRef}
        className="w-full max-w-4xl aspect-square rounded-2xl overflow-hidden shadow-2xl border border-gray-800 bg-black/50 backdrop-blur-sm"
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      />

      <div className="mt-8 flex flex-col sm:flex-row gap-4 items-center">
        <Button
          onClick={handleScramble}
          className="bg-gradient-to-r from-red-500 to-orange-500 text-white hover:opacity-90 px-8 py-3 text-lg font-semibold rounded-full transition-all duration-200 transform hover:scale-105 shadow-lg"
        >
          üé≤ Mezclar
        </Button>
        <Button
          onClick={handleReset}
          className="bg-gradient-to-r from-blue-500 to-purple-500 text-white hover:opacity-90 px-8 py-3 text-lg font-semibold rounded-full transition-all duration-200 transform hover:scale-105 shadow-lg"
        >
          ‚Ü∫ Reiniciar
        </Button>
      </div>

      <LayerSelector />

      <div className="mt-6 text-center text-gray-400 text-sm">
        <p>Desarrollado con Three.js y Next.js</p>
      </div>
    </div>
  );
};

export default RubikCubeGame;
