import * as THREE from 'three';

// Constants for cube colors
export const CUBE_COLORS = {
  front: 0xff0000,   // Red
  back: 0xffa500,    // Orange
  top: 0xffffff,     // White
  bottom: 0xffff00,  // Yellow
  right: 0x00ff00,   // Green
  left: 0x0000ff,    // Blue
};

// Create a single cubie (small cube)
export const createCubie = (x: number, y: number, z: number, colors: any, size: number = 1) => {
  const geometry = new THREE.BoxGeometry(size, size, size);
  const materials = [
    new THREE.MeshPhongMaterial({ color: colors.right, shininess: 30 }), // Right
    new THREE.MeshPhongMaterial({ color: colors.left, shininess: 30 }),  // Left
    new THREE.MeshPhongMaterial({ color: colors.top, shininess: 30 }),   // Top
    new THREE.MeshPhongMaterial({ color: colors.bottom, shininess: 30 }), // Bottom
    new THREE.MeshPhongMaterial({ color: colors.front, shininess: 30 }),  // Front
    new THREE.MeshPhongMaterial({ color: colors.back, shininess: 30 }),   // Back
  ];

  const cubie = new THREE.Mesh(geometry, materials);
  cubie.position.set(x, y, z);
  cubie.castShadow = true;
  cubie.receiveShadow = true;

  // Store the original colors and position for reference
  (cubie as any).originalColors = { ...colors };
  (cubie as any).originalPosition = { x, y, z };
  
  return cubie;
};

// Get initial colors for a cubie based on its position
const getInitialCubieColors = (x: number, y: number, z: number) => {
  return {
    right: x === 1 ? CUBE_COLORS.right : 0x333333,
    left: x === -1 ? CUBE_COLORS.left : 0x333333,
    top: y === 1 ? CUBE_COLORS.top : 0x333333,
    bottom: y === -1 ? CUBE_COLORS.bottom : 0x333333,
    front: z === 1 ? CUBE_COLORS.front : 0x333333,
    back: z === -1 ? CUBE_COLORS.back : 0x333333,
  };
};

// Create the full Rubik's Cube (3x3x3)
export const createRubiksCube = () => {
  const group = new THREE.Group();
  const size = 1;
  const gap = 0.1;

  // Create all 27 cubies
  for (let x = -1; x <= 1; x++) {
    for (let y = -1; y <= 1; y++) {
      for (let z = -1; z <= 1; z++) {
        const colors = getInitialCubieColors(x, y, z);
        const cubie = createCubie(
          x * (size + gap),
          y * (size + gap),
          z * (size + gap),
          colors,
          size
        );
        group.add(cubie);
      }
    }
  }

  // Initial rotation for better view
  group.rotation.x = Math.PI / 6;
  group.rotation.y = -Math.PI / 4;

  return group;
};

// Function to rotate a face
export const rotateFace = (cube: THREE.Group, axis: 'x' | 'y' | 'z', layer: number, angle: number) => {
  const rotationMatrix = new THREE.Matrix4();
  const pivotPoint = new THREE.Vector3(0, 0, 0);
  
  // Select cubies in the correct layer
  const cubiesInLayer = cube.children.filter((cubie) => {
    const position = cubie.position.clone();
    cube.localToWorld(position);
    const coord = position[axis];
    return Math.abs(coord - layer) < 0.1;
  });

  // Create rotation matrix
  switch (axis) {
    case 'x':
      rotationMatrix.makeRotationX(angle);
      break;
    case 'y':
      rotationMatrix.makeRotationY(angle);
      break;
    case 'z':
      rotationMatrix.makeRotationZ(angle);
      break;
  }

  // Apply rotation to selected cubies
  cubiesInLayer.forEach((cubie) => {
    cubie.position.sub(pivotPoint);
    cubie.position.applyMatrix4(rotationMatrix);
    cubie.position.add(pivotPoint);
    cubie.rotateOnWorldAxis(new THREE.Vector3(axis === 'x' ? 1 : 0, axis === 'y' ? 1 : 0, axis === 'z' ? 1 : 0), angle);
  });
};

// Function to scramble the cube
export const scrambleCube = (cube: THREE.Group) => {
  const axes: Array<'x' | 'y' | 'z'> = ['x', 'y', 'z'];
  const layers = [-1, 0, 1];
  const angles = [Math.PI / 2, -Math.PI / 2, Math.PI];
  
  // Apply 20 random moves
  for (let i = 0; i < 20; i++) {
    const axis = axes[Math.floor(Math.random() * axes.length)];
    const layer = layers[Math.floor(Math.random() * layers.length)];
    const angle = angles[Math.floor(Math.random() * angles.length)];
    rotateFace(cube, axis, layer, angle);
  }
};

// Function to check if the cube is solved
export const isSolved = (cube: THREE.Group): boolean => {
  const faces = ['right', 'left', 'top', 'bottom', 'front', 'back'];
  
  for (const face of faces) {
    const faceColor = (cube.children[0] as any).originalColors[face];
    const faceCubies = cube.children.filter((cubie) => {
      const materials = (cubie as THREE.Mesh).material as THREE.MeshPhongMaterial[];
      const faceIndex = faces.indexOf(face);
      return materials[faceIndex].color.getHex() === faceColor;
    });
    
    if (faceCubies.length !== 9) {
      return false;
    }
  }
  
  return true;
};

// Function to reset cube to initial state
export const resetCube = (cube: THREE.Group) => {
  cube.rotation.set(Math.PI / 6, -Math.PI / 4, 0);
  
  cube.children.forEach((cubie) => {
    const originalPosition = (cubie as any).originalPosition;
    const originalColors = (cubie as any).originalColors;
    
    if (originalPosition && originalColors) {
      cubie.position.set(
        originalPosition.x,
        originalPosition.y,
        originalPosition.z
      );
      
      const materials = (cubie as THREE.Mesh).material as THREE.MeshPhongMaterial[];
      materials[0].color.setHex(originalColors.right);
      materials[1].color.setHex(originalColors.left);
      materials[2].color.setHex(originalColors.top);
      materials[3].color.setHex(originalColors.bottom);
      materials[4].color.setHex(originalColors.front);
      materials[5].color.setHex(originalColors.back);
    }
  });
};

// Function to handle cube rotation based on mouse/touch movement
export const rotateCube = (
  cube: THREE.Group,
  deltaX: number,
  deltaY: number,
  sensitivity: number = 0.01
) => {
  cube.rotation.y += deltaX * sensitivity;
  cube.rotation.x += deltaY * sensitivity;
};
